<script>
  import { smoothScrollTo } from "./../utils";
  import Icon from "./Icon.svelte";

  let searchValue = "";
  let isSearching = false;
  let isEmailSignupOpen = false;
  let isOpen = false;
  let searchElement;
  let lastScrollY = null;
  let isShowing = true;

  const focusInput = () => {
    if (!searchElement) return;
    if (!isSearching) return;
    setTimeout(() => {
      searchElement.focus();
    }, 100);
  };
  $: isSearching, focusInput();

  const onSubmit = e => {
    e.preventDefault();
    console.log(
      `http://www.impactlab.org/?s=${searchValue.replace(/ /g, "+")}`
    );
    window.location.href = `http://www.impactlab.org/?s=${searchValue.replace(
      / /g,
      "+"
    )}`;
  };

  const onScroll = e => {
    const currentScrollY = window.scrollY;
    if (!Number.isFinite(lastScrollY)) {
      lastScrollY = currentScrollY;
      return;
    }
    const isGoingDown = lastScrollY > currentScrollY;
    isShowing = isGoingDown;
    lastScrollY = currentScrollY;
  };
</script>

